header {
    height: 187px;
    padding: 134px 0 0 0;
    background-color: $background-color1;
    box-sizing: border-box;
    overflow: visible;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
	.layout {
		overflow: visible;
	}
	.logo {
		display: block;
		position: absolute;
		top: -64px;
		left: 50%;
		width: 105px;
		height: 90px;
		margin: 0 0 0 -52px;
		h1 {
			text-indent: -9999px;
		}
		a {
			width: 105px;
			height: 90px;
			display: block;
			img {
				width: 100%;
				height: 100%;
				display: block;
			}
		}
	}
	nav {
		float: left;
		ul {
			display: block;

			li {
				float: left;
				margin: 0 40px 0 0;
				a {
					font-family: "Roboto", sans-serif ;
					color: $color2;
					font-size: 16px;
					font-weight: 400;
				}
			}
			&:hover {
				border-bottom: 2px solid $color3;
				padding: 0 0 6px 0;
			}
		 }
	 }
	.menu-btn {
		display: none;
	}
	.info {
		float: right;
		ul {
			display: block;
			li {
				float: left;
				margin: 0 65px 0 0;
				p {
					color: $color2;
					float: left;
				}
				img {
					float: left;
					margin: 0 9px 0 0;
				}
			}
		}
	 }


}

@media (max-width: 767px) {
    .layout {
        display: block;
        position: relative;
        overflow: hidden;
        width: 667px;
        margin:0 auto 0 auto;
		header {
			height: 187px;
			padding: 137px 0 0 0;
			background-color: $background-color1;
			box-sizing: border-box;
			overflow: visible;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 5;
			nav {
				display: none;
			}
			.info {
				display: none;
			}
			.menu-btn {
				display: block;
				width: 50px;
				height: 50px;
				position: relative;
				.menu-btn_burger {
					display: block;
					overflow: visible;
					width: 30px;
					height: 4px;
					background: $color3;
					margin: 23px auto 23px auto;
					position: relative;
				}
				&:before {
					content: '';
					position: absolute;
					bottom: 14px;
					left: 0;
					z-index: 2;
					width: 30px;
					height: 4px;
					background-color: $color3;
					display: block;
					overflow: visible;
				}
				&:after {
					content: '';
					position: absolute;
					top: 14px;
					left: 0;
					z-index: 2;
					width: 30px;
					height: 4px;
					background-color: $color3;
					display: block;
					overflow: visible;
				}
			}
		}
    }
    
}

